import React, { useEffect, useState } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {useNavigate} from "react-router-dom";
import {ColorRing} from "react-loader-spinner";
import {upperFirst, lowerCase} from 'lodash';

export default function Cat() {
  const data = [
    "TECNOLOGY",
    "ENVIRONMENT",
    "POLITICS",
    "LIFESTYLE",
    "BUSINESS",
    "HEALTH",
    "EDUCATION",
    "FASHION",
    "INTERNATIONAL",
  ];

  // console.log(Data);

  const navigate = useNavigate();

  function handleClick(e) {
    navigate("/category/"+e, {state: {catname: e}});
  }

  function handletop(e){
    navigate("/article/"+e)
  }
  const [load,setload] = useState(true)
  const [records, setRecords] = useState([]);

  useEffect(() => {
    async function getRecords() {
      const response = await fetch(`https://newtest-x5i0.onrender.com/`);
      if (!response.ok) {
        const message = `An error occurred: ${response.statusText}`;
        window.alert(message);
        return;
      }
      const records = await response.json();
      setRecords(records.data);
      setload(false)
    }
    getRecords();
    return;
  },[records.length]);

  const showCat = () => {
    return data.map((name, index) => {
      const capname = name;
      const Name = name.toLowerCase();

      function Topthree() {
        return records.map((topa, index) => {
          const orgName = topa.Title;
           const NewName = upperFirst(lowerCase(orgName))
          if (Name === topa.cat && topa.__v <= 27 ) {
            return (
              <div onClick={()=>handletop(topa.Title)} className="cat-art">
                <div className="cat-img-con">
                  <img
                    className="cat-img"
                    src={topa.Img1}
                    alt="check your connection"
                  ></img>
                </div>
                <div className="cat-text">
                  {NewName}
                </div>
              </div>
            );
          }
          else return <div></div>
        });
      }

      return (
        <Col className="cat-org">
          <div
            style={{
              marginTop: "50px",
              marginBottom: "5px",
              fontSize: 18,
              fontWeight: "600",
              letterSpacing: 2,
              color: "#8b8b8b",
            }}
          >
            {capname}
          </div>
          <div class="tit-ul2" style={{marginBottom: "30px"}}></div>
          {load ? (
      <ColorRing
  visible={true}
  height="80"
  width="80"
  ariaLabel="blocks-loading"
  wrapperStyle={{}}
  wrapperClass="blocks-wrapper"
  colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
/>
): (Topthree())}
          <div onClick={() => handleClick(Name)} className="more-btn">
            MORE
          </div>
        </Col>
      );
    });
  };

  return (
    <Container className="cat-con">
      <Row className="cat-box" sm={1} xs={1} md={2} lg={3}>
        {showCat()}
      </Row>
    </Container>
  );
}
