import React, { useEffect, useState } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {useLocation, useNavigate} from "react-router-dom";
import {ColorRing} from "react-loader-spinner";
import {upperFirst, lowerCase} from 'lodash';

export default function Category() {

  const {state} = useLocation();
  const {catname} = state;
  const [load,setload] = useState(true)
  // console.log(catname);
  const navigate = useNavigate();
  const [records, setRecords] = useState([]);
  
  useEffect(() => {
    async function getRecords() {
      const response = await fetch(`https://newtest-x5i0.onrender.com/cat/${catname}`);
      if (!response.ok) {
        const message = `An error occurred: ${response.statusText}`;
        window.alert(message);
        console.log(catname);
        return;
      }
      const records = await response.json();
      setRecords(records.data);
      setload(false)
    }
    getRecords();
    return;
  },[records.length]);

  function handleClick(e){
    navigate("/article/"+e)
  }

  function showAll() {
    return records.map((name, index) => {
      const orgName = name.Title;
      const NewName = upperFirst(lowerCase(orgName))
      if (name.cat === catname) {
        const Title = name.Title;
        const img1 =  name.Img1;
        return (
          <Col className="cat-hov">
          <div onClick={()=>handleClick(Title)} className="cat-art">
            <div className="cat-img-con">
              <img

                className="cat-img"
                src={img1}
                alt="check your connection"
              ></img>
            </div>
            <div className="cat-text">{NewName}</div>
          </div>
        </Col>
        );
      }
      else return <div></div>
    });
  }
  
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        marginTop: "70px",
      }}
    >
      <div
        style={{
          marginTop: "50px",
          marginBottom: "5px",
          fontSize: 18,
          fontWeight: "600",
          letterSpacing: 2,
          color: "#8b8b8b",
          textTransform:"uppercase"
        }}
      >
        {catname}
      </div>
      <div className="tit-ul2"></div>
      <Container style={{marginTop: "50px"}}>
            <Row lg={3} sm={1} xs={1} md={2}>
              {load ? (
      <ColorRing
  visible={true}
  height="80"
  width="80"
  ariaLabel="blocks-loading"
  wrapperStyle={{}}
  wrapperClass="blocks-wrapper"
  colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
/>
): (showAll())}
            </Row>
          </Container>
    </div>
  );
}
