import React, { useEffect, useState } from "react";
import {ColorRing} from "react-loader-spinner";



export default function Dqoute() {

  
 
  const [load,setload] = useState(true)

  const [records, setRecords] = useState([]);
  const id = "64c39f635408c77357fc6f06"

  useEffect(() => {
    async function getRecords() {
      const response = await fetch(`https://newtest-x5i0.onrender.com/${id}`);
      if (!response.ok) {
        const message = `An error occurred: ${response.statusText}`;
        window.alert(message);
        return;
      }
      const records = await response.json();
      setRecords(records.data);
      setload(false)
      
    }
    getRecords();
    return;
  }, [records.length]);


  return (
    <div
      
    >{load ? (
      <ColorRing
  visible={true}
  height="80"
  width="80"
  ariaLabel="blocks-loading"
  wrapperStyle={{}}
  wrapperClass="blocks-wrapper"
  colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
/>
): (<div style={{display: "flex", flexDirection: "column", alignItems: "center"}}>
      <div style={{marginTop: 48,color:"#8b8b8b", fontSize: 18, fontWeight: 600,letterSpacing:1.5}}>
        DAY QUOTE
      </div>
      <div class="tit-ul2"></div>
      <div style={{display:"flex",flexDirection:"row",marginTop:"40px",alignItems:"center",justifyContent:"center",lineHeight:1.6,textAlign:"center"}}>
        <div className="q-text"><img alt=",," height={20} style={{marginTop:"-20px"}} src={"./images/quote1.png"}></img>{records.dq}<img alt=",," height={20} style={{marginTop:"-20px"}} src={"./images/quote2.png"}></img></div>
      </div></div>)}
    </div>
  );
}
  