import React, {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import {ColorRing} from "react-loader-spinner";
import {upperFirst, lowerCase} from "lodash";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

export default function Topfa() {
  const navigate = useNavigate();
  function handleClick(e) {
    navigate("/article/" + e);
  }

  const [records, setRecords] = useState([]);
  const [load, setload] = useState(true);

  useEffect(() => {
    async function getRecords() {
      const response = await fetch(`https://newtest-x5i0.onrender.com/`);
      if (!response.ok) {
        const message = `An error occurred: ${response.statusText}`;
        window.alert(message);
        return;
      }
      const records = await response.json();
      setRecords(records.data);
      setload(false);
    }
    getRecords();
    return;
  }, [records.length]);

  function showFa() {
    return records.map((topfa) => {
      const orgName = topfa.Title;
      const NewName = upperFirst(lowerCase(orgName));
      if (topfa._id !== "64c39f635408c77357fc6f06") {
        return (
          <Col style={{marginTop:"30px"}}>
            <div onClick={() => handleClick(topfa.Title)} className="fa-box">
              <div className="img-con">
                <img className="img-fa" alt="plant" src={topfa.Img1}></img>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  justifyContent: "center",
                  marginTop: "40px",
                }}
              >
                <div className="tit-ul2"></div>
                <div
                  style={{
                    textAlign: "center",
                    marginTop: "15px",
                    fontSize: "14px",
                    width: "90%",
                  }}
                >
                  {NewName}
                </div>
              </div>
            </div>
          </Col>
        );
      }
    });
  }

  return (
    <div
      style={{display: "flex", justifyContent: "center", alignItems: "center"}}
    >
      {load ? (
        <ColorRing
          visible={true}
          height="80"
          width="80"
          ariaLabel="blocks-loading"
          wrapperStyle={{}}
          wrapperClass="blocks-wrapper"
          colors={["#e15b64", "#f47e60", "#f8b26a", "#abbd81", "#849b87"]}
        />
      ) : (
        <div className="fea-cont">
          <div
            style={{
              marginTop: "120px",
              fontSize: 18,
              fontWeight: 600,
              color: "#8b8b8b",
              letterSpacing: 2,
            }}
          >
            FEATURED ARTICLES
          </div>
          <div className="tit-ul2"></div>
          <Container
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
            fluid={false}
          >
            <Row lg={3} md={2} sm={1} xs={1} className="fa-con2" fluid={true}>
              {showFa()}
            </Row>
          </Container>
        </div>
      )}
    </div>
  );
}
