import React, {useEffect, useState} from "react";
import {useParams} from "react-router-dom";
import {ColorRing} from "react-loader-spinner";
import {upperFirst, lowerCase} from 'lodash';

export default function Article() {
  const [Records, setRecords] = useState([]);
  const [load, setload] = useState(true);
  let {Title} = useParams();
  
  console.log(Title);
  useEffect(() => {
    async function getRecords() {
      const response = await fetch(`https://newtest-x5i0.onrender.com/art/${Title}`);
      if (!response.ok) {
        const message = `An error occurred: ${response.statusText}`;
        window.alert(message);
        return;
      }
      const records = await response.json();

      setRecords(records.data);
      setload(false);
      console.log(records);
    }
    getRecords();
    
    return;
  },[Records.length]);

  console.log(`${Records.Title}`);
  
  function showArt() {

    return ( Records.map((name,index)=>{

      const orgTit = name.Title;
      const NewTitle = upperFirst(lowerCase(orgTit));
      return(
        <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column",
        }}
      >
        <div
          className="art-con"
          style={{
            marginBottom: "30px",
            marginTop: "70px",
          }}
        >
          <img className="art-img" src={name.Img1} alt="check your connection"></img>
          <div className="art-img-bg"></div>
          <div className="tit-box">
            <div className="art-tit">
              {NewTitle}
              <div
                className="tit-ul2"
                style={{
                  bottom: "-30px",
                  position: "absolute",
                  backgroundColor: "white",
                }}
              ></div>
            </div>
          </div>
        </div>
        <div className="art-con2">
          <div className="para">{name.Para1}</div>
          <div className="art-con">
            <img className="art-img" src={name.Img2} alt="check your connection"></img>
          </div>
          <div className="para">{name.Para2}</div>
          <div className="art-con">
            <img className="art-img" src={name.Img3} alt="check your connection"></img>
          </div>
          <div className="para">{name.Para3}</div>
        </div>
      </div>
      )
    })
      
    );
  }

  // console.log(Data.environment.Id);

  return (
    <div style={{display:"flex",justifyContent:"center",alignItems:"center"}}>
      {load ? (<div style={{height:300,display:"flex",alignItems:"center",justifyContent:"center"}}>
        <ColorRing
          visible={true}
          height="100"
          width="100"
          ariaLabel="blocks-loading"
          wrapperStyle={{}}
          wrapperClass="blocks-wrapper"
          colors={["#e15b64", "#f47e60", "#f8b26a", "#abbd81", "#849b87"]}
        /></div>
      ) : (
        <div>{showArt()}</div>
      )}
    </div>
  );
}
